import axios from "axios";

import { useContext, useState, useEffect, useCallback } from "react";
import { AppContext } from "@context/AppContext";
import { toast } from "react-toastify";
import SearchBar from "@components/SearchBar";
import MasonryListing from "@components/MasonryListing";
import SkeletonLoading from "@components/SkeletonLoading";
import Spinner from "@components/Spinner";
import Suggestions from "@components/Suggestions";
import SlickListing from "@components/SlickListing";
import InstagramIcon from '@components/Icons/InstagramIcon';
import TwitterIcon from '@components/Icons/TwitterIcon';
import SettingIcon from '@components/Icons/SettingIcon';

import LexicaSearchService from "@services/LexicaSearch";
import ReplicateInferenceService from "@services/ReplicateDiffusionInference";
import { APIDomain, ReplicateSDModelList } from "@config/const";

import { isLexicaResponse } from "@models/LexicaModel";
import IImage, { isIImage } from "@models/IImage";
import StyleSuggestion from "./StyleSuggestion";
// import SDInferenceService from "@/services/StableDiffusionInference";
import SDInferenceServiceV2, { convertSDResponseToImageGallery } from "@services/StableDiffusionInferenceV2";

import useSticky from "@hooks/useSticky";
import useClickOutside from '@hooks/useclickOutside';

import Logo from '@assets/Logo.svg';

import {
    SearchContainer,
    LogoWithSearch,
    Title,
    TextCount,
    SecondTitle,
    SignInBtn,
    SocialBtn,
    SearchWrapper,
    SettingBtn,
} from './index.styled';

import TrendingService from "@/services/TrendingService";

const MAX_LIMIT_GENERATE = 4;

const HomePage = () => {
    const { sticky, stickyRef } = useSticky();

    const {
        setIsOpenModal,
        setSelectedImage,
        isLoadingLexica, setIsLoadingLexica,
        isLoadingSD, setIsLoadingSD,
        isOpenModal
    } = useContext(AppContext);

    const [prompt, setPrompt] = useState<string>("");

    const [isLoadingTrend, setIsLoadingTrend] = useState<boolean>(true);

    const [flag, setFlag] = useState<boolean>(false);

    const [isSubmit, setIsSubmit] = useState<boolean>(false);

    const [isLoadingRunpod, setIsLoadingRunpod] = useState<boolean>(false);
    
    const [isLoadingRunpodAfter, setIsLoadingRunpodAfter] = useState<boolean>(false);

    const [isLoadingMJ, setIsLoadingMJ] = useState<boolean>(false);
    
    const [isLoadingMJAfter, setIsLoadingMJAfter] = useState<boolean>(false);
    
    const [isLoadingSDAfter, setIsLoadingSDAfter] = useState<boolean>(false);

    const [isClickInput, setIsClickInput] = useState<boolean>(false);

    // Trending
    const [displayTrendImages, setDisplayTrendingImages] = useState<IImage[] | null>(null);

    // Lexica 
    const [displayLexicaImages, setDisplayLexicaImages] = useState<IImage[] | null>(null);

    // Stable Diffusion Replicate
    const [displaySDImages, setDisplaySDImages] = useState<IImage[] | null>(null);

    // Open Midjourney Replicate
    const [displayMJImages, setDisplayMJImages] = useState<IImage[] | null>(null);

    // MJv4 Runpod
    const [displayMJv4Images, setDisplayMJv4Images] = useState<IImage[] | null>(null);

    // SDv15 Runpod
    const [displaySDv15Images, setDisplaySDv15Images] = useState<IImage[] | null>(null);

    // SDv2 Runpod
    const [displaySDv2Images, setDisplaySDv2Images] = useState<IImage[] | null>(null);

    // SDv21 Runpod
    const [displaySDv21Images, setDisplaySDv21Images] = useState<IImage[] | null>(null);


    // Styles
    const [suggestStyles, setSuggestStyles] = useState<{ [key: string]: string }>({})
    
     // eslint-disable-next-line
    const wrapOnClickEventFodData = (data: any) => {
        return data.map((item: any) => {
            return {
                ...item,
                onClickItem: () => {
                    setSelectedImage(item);
                    setIsOpenModal(true);
                }
            }
        });
    }

    const handlePromptChange = (event: any) => {
        if (event.key !== "Enter") setPrompt(event.target.value)
    }

    const fetchTrend = useCallback(async () => {
        setIsLoadingTrend(true);
        setDisplayTrendingImages(null);
        let res = await TrendingService();
        res = wrapOnClickEventFodData(res);
        if (!isLexicaResponse(res)) {
            setDisplayTrendingImages(res);
            setSuggestStyles(StyleSuggestion(res as any, 10));
        }
        setIsLoadingTrend(false);
    }, [wrapOnClickEventFodData]);
    
    useEffect(() => {
        fetchTrend();
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []);
    
    const fetchMJService = async (prompt: string, total: number, callback: any) => {
        if (total !== 1) {
            setIsLoadingMJAfter(true);
        }
        ReplicateInferenceService(prompt,
            ReplicateSDModelList.OPEN_MIDJOURNEY.model,
            ReplicateSDModelList.OPEN_MIDJOURNEY.version,
            total, undefined, "Open Midjourney", false
        ).then((res: any) => {
            let errorCount = 0;
            let result_sd: any[] = [];
            for (let idx = 0; idx < res.length; idx++) {
                if (isIImage(res[idx])) {
                    result_sd = result_sd.concat(res[idx]);
                }
                else {
                    console.log("Error result : ", res[idx])
                    errorCount += 1;
                }
            }

            setDisplayMJImages((previousState) => {
                result_sd = wrapOnClickEventFodData(result_sd);
                if (previousState === null) return result_sd;
                return total === 1 ? result_sd : [...previousState, ...result_sd];
            });

            // if all Diffusion API failed then notify user
            if (errorCount === res.length) {
                toast.error("Unable to inference Stable Diffusion")
            }
        }).finally(() => {
            setIsLoadingMJ(false);
            if (total !== 1) {
                setIsLoadingMJAfter(false);
            }
            if (callback) {
                callback();
            }
        });
    }
    
    const fetchSDService = async (prompt: string, total: number, callback: any) => {
        if (total !== 1) {
            setIsLoadingSDAfter(true);
        }
        ReplicateInferenceService(prompt,
            ReplicateSDModelList.STABLE_DIFFUSION.model,
            ReplicateSDModelList.STABLE_DIFFUSION.version,
            total, undefined, "Stable Diffusion", false
        ).then((res: any) => {
            let errorCount = 0;
            let result_sd: any[] = [];
            for (let idx = 0; idx < res.length; idx++) {
                if (isIImage(res[idx])) {
                    result_sd = result_sd.concat(res[idx]);
                }
                else {
                    console.log("Error result : ", res[idx])
                    errorCount += 1;
                }
            }

            setDisplaySDImages((previousState) => {
                result_sd = wrapOnClickEventFodData(result_sd);
                if (previousState === null) return result_sd;
                return total === 1 ? result_sd : [...previousState, ...result_sd];
            });

            // if all Diffusion API failed then notify user
            if (errorCount === res.length) {
                toast.error("Unable to inference Stable Diffusion")
            }
        }).finally(() => {
            setIsLoadingSD(false);
            if (total !== 1) {
                setIsLoadingSDAfter(false);
            }
            if (callback) {
                callback();
            }
        });
    }
    
    const fastInferenceV2 = async (prompt: string) => {
        // Fetch N=1 first
        fetchSDService(prompt, 1, () => {
            // Fetch N=4 then
            fetchSDService(prompt, MAX_LIMIT_GENERATE, null);
        });
        
        // Fetch N=1 first
        fetchMJService(prompt, 1, () => {
             // Fetch N=4 then
            fetchMJService(prompt, MAX_LIMIT_GENERATE, null);
        });
    }
    
    const fetchRunpodService = async (prompt: string, total: number, callback: any) => {
        if (total !== 1) {
            setIsLoadingRunpodAfter(true);
        }
        Promise.all([
            SDInferenceServiceV2(prompt, APIDomain.RUNPOD_SD_API_MJv4, total, "Midjourney SD success"),
            SDInferenceServiceV2(prompt, APIDomain.RUNPOD_SD_API_SDv15, total, "Stable Diffusion v1.5 success"),
            SDInferenceServiceV2(prompt, APIDomain.RUNPOD_SD_API_SDv2, total, "Stable Diffusion v2.0 success"),
            SDInferenceServiceV2(prompt, APIDomain.RUNPOD_SD_API_SDv21, total, "Stable Diffusion v2.1 success"),
        ]).then(axios.spread((...res) => {
            const [resMJv4, resSDv15, resSDv2, resSD21] = res;
            let convertResMJv4 = convertSDResponseToImageGallery(resMJv4.data, 'Midjourney');
            let convertResSDv15 = convertSDResponseToImageGallery(resSDv15.data, 'SD v1.5');
            let convertResSDv2 = convertSDResponseToImageGallery(resSDv2.data, 'SD v2.0');
            let convertResSDv21 = convertSDResponseToImageGallery(resSD21.data, 'SD v2.1');
            setDisplayMJv4Images((previousState) => {
                convertResMJv4 = wrapOnClickEventFodData(convertResMJv4);
                if (previousState === null) return convertResMJv4;
                return total === 1 ? convertResMJv4 : [...previousState, ...convertResMJv4];
            });
            setDisplaySDv15Images((previousState) => {
                convertResSDv15 = wrapOnClickEventFodData(convertResSDv15);
                if (previousState === null) return convertResSDv15;
                return total === 1 ? convertResSDv15 : [...previousState, ...convertResSDv15];
            });
            setDisplaySDv2Images((previousState) => {
                convertResSDv2 = wrapOnClickEventFodData(convertResSDv2);
                if (previousState === null) return convertResSDv2;
                return total === 1 ? convertResSDv2 : [...previousState, ...convertResSDv2];
            });
            setDisplaySDv21Images((previousState) => {
                convertResSDv21 = wrapOnClickEventFodData(convertResSDv21);
                if (previousState === null) return convertResSDv21;
                return total === 1 ? convertResSDv21 : [...previousState, ...convertResSDv21];
            });
        })).catch((error) => {
            toast.error("Unknown error, please contact developer...");
            console.log(error);
        }).finally(() => {
            setIsLoadingRunpod(false);
            if (total !== 1) {
                setIsLoadingRunpodAfter(false);
            }
            if (callback) {
                callback();
            }
        });;
    }

    const slowInferenceV2 = async (prompt: string) => {
        // Fetch N=1 first
        fetchRunpodService(prompt, 1, () => {
            // Fetch N=4 then
            fetchRunpodService(prompt, MAX_LIMIT_GENERATE, null);
        });
    }

    const handleSearchParallel = async (value: string) => {
        if (!value) {
            setIsSubmit(false);
            setDisplayLexicaImages(null);
            setDisplaySDImages(null);
            await fetchTrend();

            return;
        }

        setIsLoadingLexica(true);
        setIsLoadingRunpod(true);
        setIsLoadingSD(true);
        setIsLoadingMJ(true);
        setIsSubmit(true);
        setDisplayTrendingImages(null);

        // Priority result from Lexica first 
        setDisplayLexicaImages([]);
        let result_lexica = await LexicaSearchService(prompt);
        result_lexica = wrapOnClickEventFodData(result_lexica);
        if (!isLexicaResponse(result_lexica)) {
            setDisplayLexicaImages(result_lexica);
            setSuggestStyles(StyleSuggestion(result_lexica, 10));
        }
        setIsLoadingLexica(false);

        // After that, get result from SD API all same time
        fastInferenceV2(prompt);
        slowInferenceV2(prompt);

    }

    const handleToggle = (toggled: boolean) => {
        setFlag(toggled);
    };

    const handleAddSuggestion = (text: string) => {
        setPrompt(prompt + " " + text);
    };

    const handleClickInput = () => {
        setIsClickInput(true);
    };

    const handleClickOutsideInput = () => {
        setIsClickInput(false);
    };
    
    const handleClearInput = () => {
        setPrompt('');
    };

    useClickOutside(stickyRef, handleClickOutsideInput);

    const conditionDisplaySuggestion = !sticky || (sticky && isClickInput);

    return (
        <div>
            <SearchContainer ref={stickyRef} className={isOpenModal ? '' : `${sticky ? 'sticky' : ''}`}>
                <LogoWithSearch className={`${sticky ? 'sticky' : ''}`} alt="Logo" src={Logo} />
                <SearchWrapper>
                    <div className="flex items-center">
                        <SearchBar isDynamicHeight hiddenToggle={!conditionDisplaySuggestion} onClear={handleClearInput} onClickInput={handleClickInput} onToggleAI={handleToggle} onSubmit={handleSearchParallel} onChange={handlePromptChange} value={prompt} toggled={flag} />
                        <SettingBtn>
                            <SettingIcon />
                        </SettingBtn>
                    </div>
                </SearchWrapper>
                {(Object.entries(suggestStyles).length !== 0 && conditionDisplaySuggestion) && <Suggestions title={isSubmit ? 'Add following suggestions to make search results better' : 'Trending prompt'} items={suggestStyles} onClickItem={(_, value) => handleAddSuggestion(value)} />}
                <SocialBtn className={`${sticky ? 'instagram sticky' : 'instagram'}`}>
                    <InstagramIcon />
                </SocialBtn>
                <SocialBtn className={`${sticky ? 'twitter sticky' : 'twitter'}`}>
                    <TwitterIcon />
                </SocialBtn>
                <SignInBtn className={`${sticky ? 'sticky' : ''}`}> + Create</SignInBtn>
            </SearchContainer>
            
            <div>
                {(!isLoadingTrend && (displayTrendImages !== null))
                    ? <MasonryListing items={displayTrendImages} />
                    : isLoadingTrend && <SkeletonLoading count={6} />}
            </div>

            {isSubmit && <div>
                <Title>
                    Similar results <TextCount>({displayLexicaImages?.length || 0})</TextCount>
                </Title>
                {(!isLoadingLexica && (displayLexicaImages !== null))
                    ? <SlickListing items={displayLexicaImages} />
                    : isLoadingLexica && <Spinner />}
            </div>}

            {isSubmit && <Title>
                Art made for you
            </Title>}

            {isSubmit && <div>
                <SecondTitle>
                    Stable Disffusion 1.5 Replicate
                </SecondTitle>
                {isLoadingSDAfter && (<Spinner />)}
                {(!isLoadingSD && (displaySDImages !== null))
                    ? <SlickListing items={displaySDImages} />
                    : isLoadingSD && <SkeletonLoading count={5} />}
            </div>}

            {isSubmit && <div>
                <SecondTitle>
                    Open Midjourney Replicate
                </SecondTitle>
                {isLoadingMJAfter && (<Spinner />)}
                {(!isLoadingMJ && (displayMJImages !== null))
                    ? <SlickListing items={displayMJImages} />
                    : isLoadingMJ && <SkeletonLoading count={5} />}
            </div>}

            {isSubmit && <div>
                <SecondTitle>
                    Midjourney SD Runpod
                </SecondTitle>
                {isLoadingRunpodAfter && (<Spinner />)}
                {(!isLoadingRunpod && (displayMJv4Images !== null))
                    ? <SlickListing items={displayMJv4Images} />
                    : isLoadingRunpod && <SkeletonLoading count={5} />}
            </div>}

            {isSubmit && <div>
                <SecondTitle>
                    Stable Diffusion v1.5 Runpod
                </SecondTitle>
                {isLoadingRunpodAfter && (<Spinner />)}
                {(!isLoadingRunpod && (displaySDv15Images !== null))
                    ? <SlickListing items={displaySDv15Images} />
                    : isLoadingRunpod && <SkeletonLoading count={5} />}
            </div>}

            {isSubmit && <div>
                <SecondTitle>
                    Stable Diffusion v2.0 Runpod
                </SecondTitle>
                {isLoadingRunpodAfter && (<Spinner />)}
                {(!isLoadingRunpod && (displaySDv2Images !== null))
                    ? <SlickListing items={displaySDv2Images} />
                    : isLoadingRunpod && <SkeletonLoading count={5} />}
            </div>}

            {isSubmit && <div>
                <SecondTitle>
                    Stable Diffusion v2.1 Runpod
                </SecondTitle>
                {isLoadingRunpodAfter && (<Spinner />)}
                {(!isLoadingRunpod && (displaySDv21Images !== null))
                    ? <SlickListing items={displaySDv21Images} />
                    : isLoadingRunpod && <SkeletonLoading count={5} />}
            </div>}
        </div>
    )
}

export default HomePage
