import { createContext } from 'react'
import IImage from '@models/IImage'

export type AppContextType = {
  selectedImage: IImage | null,
  setSelectedImage: (v: IImage | null) => void
  isOpenModal: boolean
  setIsOpenModal: (v: boolean) => void, 
  isLoadingLexica: boolean, 
  setIsLoadingLexica: (v:boolean) => void, 
  isLoadingSD: boolean, 
  setIsLoadingSD: (v:boolean) => void, 

}

export const AppContext = createContext<AppContextType>({
  selectedImage: null, 
  setSelectedImage: () => {}, 
  isOpenModal: false, 
  setIsOpenModal: () => {}, 
  isLoadingLexica: false, 
  setIsLoadingLexica: () => {}, 
  isLoadingSD: false, 
  setIsLoadingSD: () => {}
})
