interface ILexicaImage {
    id: string,
    gallery: string,
    src: string,
    srcSmall: string,
    prompt: string,
    width: number,
    height: number,
    seed: string,
    grid: boolean,
    model: string,
    guidance: string,
    promptid: string,
    nsfw: string, 
    style?: string  // local API will have this 
                    // Lexica API don't  
}

interface LexicaRequest { 
    k: number, // Top K 
    prompt: string
}

interface LexicaResponse {
    error?: boolean, 
    message?: string, 
    data: ILexicaImage[]
}

export type { ILexicaImage, LexicaResponse, LexicaRequest }


export const isLexicaResponse = (object: any): object is LexicaResponse => {
    return "id" in object;
} 
