
import PropTypes from 'prop-types';

import { Menu, MenuItem, MenuButton } from '@szhsin/react-menu';
import '@szhsin/react-menu/dist/index.css';
import '@szhsin/react-menu/dist/transitions/slide.css';

import DotIcon from '@components/Icons/DotIcon';

import {
  MoreBtn,
  MenuIcon
} from './index.styled';

type MoreActionProps = PropTypes.InferProps<typeof propTypes>;

const propTypes = {
  onClick: PropTypes.func,
  onSelect: PropTypes.func.isRequired,
  data: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    value: PropTypes.string,
    icon: PropTypes.element,
  })),
};

const MoreAction = ({ data, onSelect }: MoreActionProps) => {
  const handleClick = (e: any) => {
    e.stopPropagation();
  };
  
    return (
      <Menu align="start" direction="bottom" onItemClick={onSelect} onClick={handleClick} menuButton={<MenuButton onClick={handleClick}><MoreBtn><DotIcon /></MoreBtn></MenuButton>} transition>
        {data?.map(({ value = '', label = '', icon }: any) => <MenuItem value={value} key={value}><div className="flex items-center">
          <MenuIcon>{icon}</MenuIcon>
          <div>{label}</div>
        </div></MenuItem>)}
      </Menu>
    )
};

MoreAction.defaultProps = {
  data: [],
  onClick: () => {},
  onSelect: () => {},
};

export default MoreAction;
