import IImage from "@models/IImage";

const removeSpecialCharacters = (str: string): string => {
  // Remove special charcter & replace with space
  return str.replace(/[^a-zA-Z0-9]/g, ' ');
}

const StyleSuggestion = (imageList: IImage[], maxNumSuggestion: number = 5): { [key: string]: string } => {
    let styles = new Set();
    let dictionary: { [key: string]: string } = {};

    for (let idx = 0; idx < imageList.length; idx++) {
        // Get first 5 words 
        let key: string = imageList[idx].style ? (imageList[idx].style?.trim().split(' ').slice(0, 5).join(" ") ?? "")
        : (
            imageList[idx].style?.trim().split(' ').slice(0, 5).join(" ") ?? ""
        );
        key = removeSpecialCharacters(key); 

        if ((key === "" || key === undefined || styles.has(key)))
            continue

        styles.add(key);
        dictionary[key] = imageList[idx].style ? imageList[idx].style as string : imageList[idx].prompt as string
    };

    return Object.entries(dictionary)
        .slice(0, maxNumSuggestion)
        .reduce((obj, [key, value]) => ({ ...obj, [key]: value }), {});;
}

export default StyleSuggestion
