interface IImage {
    caption?: string,
    src?: string,
    width?: number,
    height?: number,
    tags?: any
    model_name?: string,
    prompt?: string,
    cfg_scale?: number,
    seed?: number,
    style?: string
}

export default IImage; 

export const isIImage = (object: any): object is IImage => {
    return ("height" in object && "width" in object);
}
