import { APIDomain } from "@config/const";
import { ILexicaImage, LexicaResponse } from "@models/LexicaModel";
import APIAxios from "./APIAxios";
import { toast } from "react-toastify";
import IImage from "@models/IImage";
import { AxiosResponse } from "axios";

const TrendingService = async (): Promise<IImage[] | LexicaResponse> => {

    const convertLexicaResponseToImageGallery = (data: ILexicaImage[]): IImage[] => {
        let image_list: IImage[] = [];

        for (let idx = 0; idx < data.length; idx++) {
            let image: IImage = {
                src: data[idx].src,
                width: data[idx].width,
                height: data[idx].height,
                tags: [
                    { value: "Stable Diffusion", title: "Stable Diffusion" },
                ],
                cfg_scale: Number(data[idx].guidance),
                caption: data[idx].prompt,
                model_name: data[idx].model,
                prompt: data[idx].prompt,
                seed: Number(data[idx].seed),
                style: data[idx].style
            }
            image_list.push(image);
        }
        return image_list;
    };

    const fetchData = async () => {
        try {
            const res: AxiosResponse = await APIAxios.get<LexicaResponse>(APIDomain.TRENDING_API)

            if (res.status !== 200) {
                console.log("Unable to fetch result from trending API: ", res.status);
                toast.error("Trending API API: Unable to fetch result");
                return res;
            }

            const data: IImage[] = convertLexicaResponseToImageGallery(res.data?.images);

            return data;

        } catch (error: any) {
            console.log("Encounter error from trending API: ", error);
            toast.error("Trending API: encounter errors...");
            return error;
        }
    }

    const result = await fetchData();
    return result;
}

export default TrendingService; 
