import { APIDomain, MAX_LEXICA_RESULT } from "@config/const";
import { ILexicaImage, LexicaRequest, LexicaResponse } from "@models/LexicaModel";
import APIAxios from "./APIAxios";
import { toast } from "react-toastify";
import IImage from "@models/IImage";
import { AxiosResponse } from "axios";

const LexicaSearchService = async (prompt: string, top_k: number = MAX_LEXICA_RESULT): Promise<IImage[] | LexicaResponse> => {

    const convertLexicaResponseToImageGallery = (data: ILexicaImage[]): IImage[] => {
        let image_list: IImage[] = [];

        for (let idx = 0; idx < data.length; idx++) {
            let image: IImage = {
                src: data[idx].src,
                width: data[idx].width,
                height: data[idx].height,
                tags: [
                    { value: "Stable Diffusion", title: "Stable Diffusion" },
                ],
                cfg_scale: Number(data[idx].guidance),
                caption: data[idx].prompt,
                model_name: data[idx].model,
                prompt: data[idx].prompt,
                seed: Number(data[idx].seed),
                style: data[idx].style
            }
            image_list.push(image);
        }
        return image_list;
    };

    const fetchData = async (prompt: string, top_k: number) => {
        const reqData: LexicaRequest = {
            prompt: prompt,
            k: top_k
        }

        try {
            const res: AxiosResponse = await APIAxios.get<LexicaResponse>(
                APIDomain.LEXICA_API,
                { params: reqData });

            if (res.status !== 200) {
                console.log("Unable to fetch result from Lexica: ", res.status);
                toast.error("Lexica API: Unable to fetch result");
                return res;
            }

            const data: IImage[] = convertLexicaResponseToImageGallery(res.data);
            // toast.success("Fetch similar result successful...");

            return data;

        } catch (error: any) {
            console.log("Encounter error from Lexica: ", error);
            toast.error("Stable Diffusion: Encounter errors...");
            return error;
        }
    }

    const result = await fetchData(prompt, top_k);
    return result; 
}

export default LexicaSearchService; 
