import 'react-toastify/dist/ReactToastify.css';
import HomePage from '@pages/HomePage';
import { ToastContainer } from 'react-toastify';
import { AppContext } from '@context/AppContext';
import ImageModal from '@components/ImageModal';
import Navbar from "@components/Navbar";
import Footer from "@components/Footer";
import GlobalStyles from './global.styled';
import { useMemo, useState } from 'react';
import IImage from '@models/IImage';

function App() {
  const [selectedImage, setSelectedImage] = useState<IImage | null>(null);
  const [isOpenModal, setIsOpenModal] = useState<boolean>(false);
  const [isLoadingLexica, setIsLoadingLexica] = useState<boolean>(false);
  const [isLoadingSD, setIsLoadingSD] = useState<boolean>(false);

  const value = useMemo(
    () => ({ 
      selectedImage, setSelectedImage, 
      isOpenModal, setIsOpenModal, 
      isLoadingLexica, setIsLoadingLexica, 
      isLoadingSD, setIsLoadingSD
   }),
    [selectedImage, isOpenModal, isLoadingLexica, isLoadingSD]
  )
  return (
    <AppContext.Provider
      value={value}
    >
      <GlobalStyles />
      <div className="App">
        <Navbar />
        <ToastContainer
          limit={3}
          autoClose={3000}
          position='top-right'
          theme='light'
        />
        <HomePage />
        <ImageModal />
        <Footer />
      </div>
    </ AppContext.Provider >
  );
}

export default App;
